import { IAddTeachingRequest } from "app/admin/models/admin/course-request";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder } from "@angular/forms";
import { Validators } from "@angular/forms";
import { CustomeValidator } from "app/shared/validators/customeValid.validator";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { CourseAndTeachingRequestService } from "app/admin/services/Admin/course-teaching-request.service";
import { CourseData, RequestData } from "app/admin/models/admin/course-request";
import { MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { MessagesService } from "app/shared/services/messages.service";
import { LanguageService } from "app/shared/services/language.service";
import { NgxSpinnerService } from "ngx-spinner";
import { Observable } from "rxjs";
import { map, startWith } from "rxjs/operators";

@Component({
  selector: "app-teaching-employee-request",
  templateUrl: "./teaching-employee-request.component.html",
  styleUrls: ["./teaching-employee-request.component.scss"],
})
export class TeachingEmployeeRequestComponent implements OnInit {
  myForm: FormGroup;
  submitted: boolean = false;
  coursesList: CourseData[];
  filteredCoursesList: Observable<CourseData[]>;
  totalItems: number;
  displayedColumns: string[] = [
    "#",
    "courseName",
    "details",
    "numberOfDays",
    "status",
    "actions",
  ];
  dataSource: MatTableDataSource<RequestData>;
  curPage: number = 1;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  constructor(
    private fb: FormBuilder,
    private courseAndTeachingRequestService: CourseAndTeachingRequestService,
    private languageService: LanguageService,
    private dialog: MatDialog,
    private toastr: MessagesService,
    private spinner: NgxSpinnerService
  ) {}

  ngOnInit(): void {
    this.myForm = this.fb.group({
      course: [null, Validators.required],
      details: ["", [Validators.required, CustomeValidator.whiteSpace]],
      numberOfDays: ["", [Validators.required, CustomeValidator.bigZero]],
    });

    this.getAllCourses();
    this.getAllUserRequests(this.curPage);
  }

  ngAfterViewInit() {
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.startSortingTable();
    }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  get FormCtrls() {
    return this.myForm.controls;
  }

  getAllCourses() {
    this.courseAndTeachingRequestService.getAllCourses().subscribe((res) => {
      if (res.returnValue == 1) {
        this.coursesList = res.Courses;
        this.filteredCoursesList = this.FormCtrls.course.valueChanges.pipe(
          startWith(""),
          map((value: any) => this.filter_course_name(value || ""))
        );
      }
    });
  }
  // filter course names
  private filter_course_name(value: string): any[] {
    const filterValue = value.toLowerCase();
    return this.coursesList.filter(
      (option) =>
        option.course_ar_name.toLowerCase().includes(filterValue) ||
        option.course_en_name.toLowerCase().includes(filterValue)
    );
  }

  getAllUserRequests(page: number) {
    this.curPage = page;
    this.spinner.show();
    this.courseAndTeachingRequestService
      .getAllUserTeachingRequests(page)
      .subscribe((res) => {
        if (res) {
          this.totalItems = res.countItems;
          this.dataSource = new MatTableDataSource(res.Requests);
          this.startSortingTable();
        }
        this.spinner.hide();
      });
  }

  AddRequest() {
    this.submitted = true;
    if (this.myForm.valid) {
      let request = {} as IAddTeachingRequest;
      request.details = this.FormCtrls.details.value;
      request.numberOfDays = this.FormCtrls.numberOfDays.value;
      let courseId =
        this.coursesList.filter(
          (course) =>
            course.course_ar_name.toLowerCase() ==
              this.FormCtrls.course.value.toLowerCase() ||
            course.course_en_name.toLowerCase() ==
              this.FormCtrls.course.value.toLowerCase()
        )[0]?.id ?? null;
      request.courseId = courseId;
      if (!courseId) request.courseName = this.FormCtrls.course.value;

      this.spinner.show();
      this.courseAndTeachingRequestService
        .addTeachingRequest(request)
        .subscribe((res: any) => {
          if (res.returnValue == 1) {
            this.toastr.success("General.SendSuccessfully");
            this.submitted = false;
            this.myForm.reset();
            this.getAllUserRequests(1);
            this.dataSource.paginator = this.paginator;
          } else this.toastr.error(res.returnString);
          this.spinner.hide();
        });
    } else this.toastr.error("General.FormNotValid");
  }

  deleteTeachingRequest(request: RequestData) {
    this.spinner.show();
    this.courseAndTeachingRequestService
      .deleteTeachingRequest(request.id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.toastr.success("General.DeleteSuccessfully");
          if (this.dataSource.data.length == 1 && this.curPage > 1) {
            // if last item in list: get previous page
            this.getAllUserRequests(this.curPage - 1);
            this.paginator.pageIndex = this.curPage - 1;
          } else {
            // if not last item in list: get current page
            this.getAllUserRequests(this.curPage);
          }
        } else this.toastr.error(res.returnString);
        this.spinner.hide();
      });
  }

  openDeleateDialog(request: RequestData): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.languageService.isArabic
            ? request.course_ar_name ?? request.course_en_name
            : request.course_en_name ?? request.course_ar_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteTeachingRequest(request);
      });
  }

  startSortingTable() {
    // custom sort
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch (property) {
        case "courseName":
          return this.languageService.isArabic
            ? item.course_ar_name ?? item.course_en_name
            : item.course_en_name ?? item.course_ar_name;
        default:
          return item[property];
      }
    };
    this.dataSource.sort = this.sort;
  }
}
