import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class TeacherSubjectsService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  //Return Home Teachers
  ReturnHomeTeachers() {
    return this.get("Teachers/HomeTeachers");
  }

  getMyTeachers<T>() {
    return this.get<T>("AJAX/Teachers/returnStudentTeachers");
  }

  //Return Teachers Payments By EduCompId
  TeachersPayments<T>() {
    return this.get<T>("Teachers/returnTeacherPaymentsTest");
  }

  //Return Center By User
  returnCenterByUser() {
    return this.get("AJAX/returnCentersByUser");
  }

  ReturnTeacherSubjects<T>(id: any) {
    return this.post<T>("Teachers/selectedTeacher", id);
  }

  ReturnTeacherData<T, TE>(data: TE) {
    return this.post<T>("Teachers/getTeacherSummary", data);
  }

  ReturnSubjectChapters<T>(id: any) {
    return this.post<T>("Teachers/TeacherSubjectData", id);
  }

  //Live Lectures
  ReturnLiveLectures<T>(data: any) {
    return this.post<T>("Teachers/LiveLectures", data);
  }

  //Lectures List
  ReturneLecturesList<T>(subCourseId: number) {
    return this.get<T>(`AJAX/materialsByEntID/${subCourseId}`);
  }

  // Offline Lectures List
  ReturneofflineLectures<T, TE>(data: TE) {
    return this.post<T>("AJAX/returnOfflineMaterialsByEntId", data);
  }

  // Exams List
  ReturneExamsList<T>(data: any) {
    return this.post<T>("AJAX/materialsByExamsID", data);
  }

  //Files List
  ReturneFilesList<T, TE>(data: TE) {
    return this.post<T>("Teachers/getDocumentFiles", data);
  }

  // Return LTeacher Subject Evaluation
  LoadTeacherSubjectEvaluation(teacher_sub_id: any) {
    return this.get("AJAX/LoadTeacherSubjectEvaluation/" + teacher_sub_id);
  }

  // Return Teacher Subjects By SubjectId
  returnTeacherSubjectsBySubjectId(pageId: any, SubjectId: any) {
    return this.get(
      `AJAX/returnTeacherSubjectsBySubjectId/${pageId}/${SubjectId}`
    );
  }

  // Return Subject Structure
  returnSubjectStructure(SubjectId: any) {
    return this.get("AJAX/viewSubjectPartialBySubjectId/" + SubjectId);
  }
}
