import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class MaterialsService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  ReturnMaterialByMatId(MaterialId: any) {
    return this.get(`Materials/getLessonPartsAndExams/${MaterialId}`);
  }

  buyMaterialByCredit(id: any) {
    return this.post("AJAX/buyMaterial/" + id, null);
  }

  subscribeMaterial(data: any) {
    return this.post("AJAX/subscribeMaterial", data);
  }

  buyOfflineMaterial(id: any) {
    return this.post("AJAX/buyOfflineMaterial/" + id, null);
  }

  //Check if Offline Material Has Online
  CheckifOfflineMaterialHasOnline(id: any) {
    return this.get("AJAX/CheckifOfflineMaterialHasOnline/" + id);
  }

  //buy Exam
  buyExam(id: any) {
    return this.get("AJAX/buyExam/" + id);
  }

  //buy Template
  buyTemplate(id: any) {
    return this.get("AJAX/buyTemplate/" + id);
  }

  //Reset Exam Student
  ResetExamAsStudent(id: any) {
    return this.get("AJAX/ResetExamAsStudent/" + id);
  }

  //Reset Template Student
  ResetTemplateAsStudent(id: any) {
    return this.get("AJAX/ResetTemplateAsStudent/" + id);
  }

  //buy Live Lecture
  bookLecture(id: any) {
    return this.post("AJAX/bookLecture/" + id, null);
  }

  // Get Quiz Times
  getQuizTimes(data: any) {
    return this.post("Materials/getQuizTimes", data);
  }

  // Get Quiz
  getQuiz(data: any) {
    return this.post("Materials/getQuiz", data);
  }

  //  Create View Row
  createViewRow(matId: any, partId: any) {
    return this.post(`APIMaterials/createViewRow/${matId}/${partId}`, null);
  }

  // increment View + 1
  incrementView(matId: any, partId: any) {
    return this.post(`APIMaterials/incrementView/${matId}/${partId}`, null);
  }
}
