import { Component, OnInit } from "@angular/core";
import { TranslateService } from "@ngx-translate/core";
import { IMyCoursesMV } from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { AuthService } from "app/shared/services/auth/auth.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-my-courses",
  templateUrl: "./my-courses.component.html",
  styleUrls: ["./my-courses.component.scss"],
})
export class MyCoursesComponent implements OnInit {
  MyCourses: IMyCoursesMV[];
  curLang: string;
  constructor(
    private spinner: NgxSpinnerService,
    public authService: AuthService,
    public courseService: CourseService,
    private translate: TranslateService
  ) {
    this.translate.onLangChange.subscribe((lang) => {
      this.curLang = lang.lang;
    });
  }

  ngOnInit(): void {
    if (this.authService.isStudent()) this.getMyCourses();
  }

  getMyCourses() {
    this.spinner.show();
    this.courseService.getMyCourses<any>().subscribe((res) => {
      this.MyCourses = res.SubCoursesList;
      this.spinner.hide();
    });
  }
}
