import { Pipe, PipeTransform } from "@angular/core";

@Pipe({
  name: "vimeoLink",
})
export class VimeoLinkPipe implements PipeTransform {
  transform(value: any): any {
    let path = value.replace("https://vimeo.com/", "/");
    let fullPath = `https://player.vimeo.com/video/${value}?amp;badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479`;
    return fullPath;
  }
}
