import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class CreateTemplateService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  getTeachers() {
    return this.get("AJAX/getTemplateCreationTeachers");
  }

  createStudentTemplate(data: any) {
    return this.post("AJAX/createStudentTemplate", data);
  }

  addTemplateDetail(data: any) {
    return this.post("AJAX/addTemplateDetail", data);
  }

  getTemplateDetails(templateId: any) {
    return this.get("AJAX/returnTemplateDetails/" + templateId);
  }

  DeleteDetails(DetailsId: any) {
    return this.delete("template/deleteTemplateDetailById/" + DetailsId);
  }
}
