import { Component, OnInit, ViewChild } from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { TranslateService } from "@ngx-translate/core";
import {
  giftGalleryViewModel,
  IUserPointsVM,
} from "app/admin/models/admin/gifts";
import { GiftsService } from "app/admin/services/Admin/gifts.service";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { MessagesService } from "app/shared/services/messages.service";
import { environment } from "environments/environment";
import { ToastrService } from "ngx-toastr";

@Component({
  selector: "app-gift-gallery",
  templateUrl: "./gift-gallery.component.html",
  styleUrls: ["./gift-gallery.component.css"],
})
export class GiftGalleryComponent implements OnInit {
  itemsCount: number = 0;
  currentLang: string;
  //giftBuy:giftViewModel[]=[];
  allPoints: number;
  usedPoints: number;
  availablePoints: number;
  giftBuy: giftGalleryViewModel[] = [];
  constructor(
    private giftServ: GiftsService,
    private translate: TranslateService,
    private toastr: MessagesService,
    public fileService: UploadFileService
  ) {}

  ngOnInit() {
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });
    this.getGiftGallery();
    this.giftServ.getPoints();

    this.giftServ?.userPoints.subscribe((point: IUserPointsVM) => {
      this.allPoints = point.Allpoints;
      this.usedPoints = point.Usedpoints;
      this.availablePoints = point.UnUsedpoints;
    });
  }

  ngAfterViewInit() {
    this.translate.onLangChange.subscribe((lang) => {
      this.currentLang = lang.lang;
    });
    this.giftServ.getPoints();
    this.getGiftGallery();
  }

  getGiftGallery() {
    this.giftServ.getGiftGallery().subscribe((res: any) => {
      this.giftBuy = res.giftGallery;
    });
  }

  Buy(giftId: number) {
    this.giftServ.buyGift(giftId).subscribe((res: any) => {
      if (res.returnValue == 1) {
        if (this.currentLang == "ar") {
          this.toastr.success("   ");
        } else {
          this.toastr.success("Gift bought successfully");
        }
        this.getGiftGallery();
        this.giftServ.getPoints();
      } else {
        if (this.currentLang == "ar") {
          this.toastr.error("  ");
        } else {
          this.toastr.error("Something went wrong");
        }
      }
    });
  }
}
