import { unit } from "app/admin/models/admin/educations";
import { StudentService } from "app/student/services/student.service";
import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { TeacherSubjectsService } from "app/student/services/teacherSubjects.service";
import { ITeacherDataPostVM, ITeacherDataVM } from "app/student/models/student";
import { TeacherAnnouncementVM } from "app/student/models/ask-teacher";
import { TeacherAnnouncementComponent } from "../teacher-announcement/teacher-announcement.component";
import { MatDialog } from "@angular/material/dialog";
import { AnnouncementService } from "app/admin/services/Admin/announcement.service";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import {
  ICourseGetVM,
  IDiplomaGetVM,
  IUnitAttachmentResponseVM,
  IUnitDataGeneralVM,
} from "app/admin/models/admin/setup/course";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { NgxSpinnerService } from "ngx-spinner";
import { SubjectsService } from "app/admin/services/Admin/subjects.service";
import { AppStrings } from "app/shared/data/app-strings";
import { AppAssets } from "app/shared/data/app-assets";
import { LanguageService } from "app/shared/services/language.service";
import { Rate, Rates } from "app/shared/data/rate-list";
import {
  RatetCourseRequest,
  RatetCourseResponse,
} from "app/student/models/shopping-cart";
import { ShoppingCartService } from "app/student/services/shopping-cart.service";
