@Component({
  selector: "app-teacher-subject-profile",
  templateUrl: "./teacher-subject-profile.component.html",
  styleUrls: ["./teacher-subject-profile.component.scss"],
})
export class TeacherSubjectProfileComponent implements OnInit {
  TeacherData?: ITeacherDataVM;
  // TopStudentsList: ITopStudentsVM[] = [];
  // TopExamStudentsList: ITopExamStudentsVM[] = [];
  teacherId: number;
  subjectId: number;
  main_courseId: number;
  course: ICourseGetVM;
  // Diploma: IDiplomaGetVM;
  stages: unit[] = [];
  attachments: IUnitAttachmentResponseVM[];
  // unitData: IUnitDataGeneralVM[];
  teacherSubjectId: number;
  teacherAnnounce: TeacherAnnouncementVM[] = [];

  EduCompId: number;
  // isChapter: boolean = true;
  rates: Rate[] = [];
  constructor(
    private TeacherSubServ: TeacherSubjectsService,
    private route: ActivatedRoute,
    private router: Router,
    private dialog: MatDialog,
    private StudentService: StudentService,
    private announcementService: AnnouncementService,
    private courseServ: CourseService,
    private subjectsService: SubjectsService,
    private shoppingCartService: ShoppingCartService,
    public fileService: UploadFileService,
    private spinner: NgxSpinnerService,
    public appStrings: AppStrings,
    public appAssets: AppAssets,
    private msg: MessagesService,
    public languageService: LanguageService
  ) {
    this.route.paramMap.subscribe((params) => {
      this.teacherId = Number(params.get("teacherId"));
      this.subjectId = Number(params.get("courseId"));
      this.teacherSubjectId = Number(params.get("teacherSubjectId"));
    });

    this.route.queryParamMap.subscribe((params) => {
      this.main_courseId = Number(params.get("main_courseId"));
    });

    // course
    this.courseServ
      .getCourseById<ICourseGetVM>(this.subjectId)
      .subscribe((res) => {
        this.course = res.sub_course;
      });
    this.rates = Rates;
    // diploma
    // this.courseServ
    //   .getDiplomaById(this.main_courseId)
    //   .subscribe((res: IDiplomaGetVM) => {
    //     this.Diploma = res;
    //   });
  }

  ngOnInit(): void {
    this.getAllStagesByCourseId();
    this.getAllCourseAttachments();
    // this.ReturneFilesList();
    // this.ReturnTeacherData(this.subjectId, this.teacherId);
    // this.ReturnTeacherAnnouncement(this.teacherId);
  }

  rateCourse(subject_id: number, rate: number) {
    let body: RatetCourseRequest = {
      subject_id: subject_id,
      rate: rate,
    };
    this.shoppingCartService
      .rateCourse(body)
      .subscribe((res: RatetCourseResponse) => {
        if (res.returnValue == 1) {
          if (res.subject_rate.totalRate)
            this.course.rate = res.subject_rate?.totalRate;
          else this.course.rate = 0;
        } else this.msg.warning(res.returnString);
      });
  }

  // return units (stages) in course
  getAllStagesByCourseId() {
    this.spinner.show();
    this.subjectsService.getUnits(this.subjectId).subscribe((res) => {
      if (res.length > 0) {
        this.stages = res;
        this.stages.forEach((unit, index) =>
          this.getAllUnitData(unit.id, index)
        );
        this.getAllCourseAttachments();
      }
      this.spinner.hide();
    });
  }

  // return data in stage (unit)
  getAllUnitData(unitId: number, index: number) {
    // this.spinner.show();
    this.courseServ.getAllUnitData(unitId).subscribe((res) => {
      if (res) {
        // exams & lectures
        let unitData: IUnitDataGeneralVM[] = res.model;
        unitData = unitData.sort((a, b) => a.order - b.order);
        this.stages[index].data = unitData;
        this.stages[index].CompletePercentageUnit = res.CompletePercentageUnit;
        // console.log("selected unit data", unitData);
      }
      // this.spinner.hide();
    });
  }

  getAllCourseAttachments() {
    this.courseServ.getAllUnitAttachments(this.subjectId).subscribe((res) => {
      if (res) this.attachments = res.UnitData.reverse();
    });
  }

  navigateToLectureOrExam(data: IUnitDataGeneralVM) {
    // console.log("navigate allowed");
    if (data.type == this.appStrings.stageLecture)
      this.router.navigate([
        `student/course/${this.subjectId}/material/${data.id}`,
      ]);
    else if (data.type == this.appStrings.stageExam)
      this.router.navigate([`student/exam/${data.id}/3`]);
  }

  checkIsAllowedToNavigate(
    stage: IUnitDataGeneralVM,
    unitIndex: number,
    currentItemIndex: number
  ) {
    // previous units
    if (unitIndex > 0) {
      let lastMandatoryItemPreviousUnits: IUnitDataGeneralVM;
      for (let i = 0; i < unitIndex; i++) {
        // get last mandatory item before current (selected)
        this.stages[i].data?.forEach((item) => {
          if (item.isMandatory) lastMandatoryItemPreviousUnits = item;
        });
      }

      if (
        lastMandatoryItemPreviousUnits &&
        !lastMandatoryItemPreviousUnits.isComplete
      )
        return false;
    }

    // get last mandatory item before current (selected)
    let lastMandatoryItem: IUnitDataGeneralVM;
    this.stages[unitIndex].data.forEach((item, index) => {
      if (index < currentItemIndex && item.isMandatory)
        lastMandatoryItem = item;
    });

    return stage.isComplete ||
      !lastMandatoryItem ||
      lastMandatoryItem?.isComplete ||
      currentItemIndex == 0
      ? true
      : false;
  }

  ReturnTeacherData(subject_id: any, teacher_ID: any) {
    let data: ITeacherDataPostVM = {
      subject_id: subject_id,
      teacherUserId: teacher_ID,
    };
    this.TeacherSubServ.ReturnTeacherData<ITeacherDataVM, ITeacherDataPostVM>(
      data
    ).subscribe((res) => {
      this.TeacherData = res[0];
      this.EduCompId = res[0]?.EduCompId;
      // if (this.teacherId == 17) {
      //   this.GetTopStudents(res[0].EduCompId, this.teacherId, this.subjectId);
      //   this.GetTopExamStudents(res[0].EduCompId, this.teacherId);
      // }
    });
  }

  // ReturneFilesList() {
  //   let data: ITeacherSubjectFilesVM = {
  //     //teacher: this.teacherId,
  //     teacherUserId: this.teacherId,
  //     subject: this.subjectId,
  //     teacher_subject: this.teacherSubjectId,
  //   };
  //   this.TeacherSubServ.ReturneFilesList<
  //     IFileDocumentVM,
  //     ITeacherSubjectFilesVM
  //   >(data).subscribe((res) => {
  //     //console.log("List", res);
  //     this.FilesList = res.materialsList;
  //   });
  // }

  // GetTopStudents(EduCompId: any, TeacherId: any, SubjectId: any) {
  //   this.StudentService.GetTopStudents<ITopStudentsVM[]>(
  //     EduCompId,
  //     TeacherId,
  //     SubjectId
  //   ).subscribe((res) => {
  //     this.TopStudentsList = res;
  //     console.log("GetTopStudents", res);
  //   });
  // }

  // GetTopExamStudents(EduCompId: any, TeacherId: any) {
  //   this.StudentService.GetTopExamStudents<ITopExamStudentsVM[]>(
  //     EduCompId,
  //     TeacherId
  //   ).subscribe((res) => {
  //     this.TopExamStudentsList = res;
  //     console.log("GetTopExamStudents", res);
  //   });
  // }

  ReturnTeacherAnnouncement(teacher_ID: number) {
    this.announcementService
      .getAnnouncements<TeacherAnnouncementVM[]>(this.teacherId)
      .subscribe((res) => {
        if (res) {
          if (res.length > 0) {
            this.teacherAnnounce = res;
            // console.log(this.teacherAnnounce);
            this.openAnnouncementDialog(this.teacherAnnounce);
          }
        }
      });
  }

  openAnnouncementDialog(item: TeacherAnnouncementVM[]): void {
    this.dialog
      .open(TeacherAnnouncementComponent, {
        data: {
          msg: item,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) console.log("dialog");
      });
  }
}
