  isAllLectureIsCompleted() {
    this.isCompleted = this.parts.every((part) => part.isComplete == true);
    // console.log(this.isCompleted);
  }

  navigateToNextOrPreviousItem(data: IUnitDataGeneralVM) {
    if (data.type == this.appStrings.stageLecture)
      this.router.navigate([
        `student/course/${this.subjectId}/material/${data.id}`,
      ]);
    else if (data.type == this.appStrings.stageExam)
      this.router.navigate([`student/exam/${data.id}/3`]);
  }
