import { LanguageService } from "app/shared/services/language.service";
import { Component, OnInit } from "@angular/core";
import {
  IMyCoursesMV,
  IUserPaidCoursesMV,
} from "app/admin/models/admin/setup/course";
import { CourseService } from "app/admin/services/Admin/setup/course.service";
import { DiplomaUserModel } from "app/home/models/home-course";
import { AppAssets } from "app/shared/data/app-assets";

@Component({
  selector: "app-employee-courses",
  templateUrl: "./employee-courses.component.html",
  styleUrls: ["./employee-courses.component.scss"],
})
export class EmployeeCoursesComponent implements OnInit {
  allCourses: IMyCoursesMV[] = [];
  allDiplomas: DiplomaUserModel[] = [];
  allData: any[];

  constructor(
    public courseService: CourseService,
    public languageService: LanguageService,
    public appAssets: AppAssets
  ) {}

  ngOnInit(): void {
    this.courseService
      .getUserPaidCourses()
      .subscribe((res: IUserPaidCoursesMV) => {
        this.allCourses = res.MyCourses;
        this.allDiplomas = res.MyDiplomas;

        this.allDiplomas?.map((ele) => {
          ele.course_Minutes = ele.totalCoursesMinutes % 60;
          ele.course_hours =
            (ele.totalCoursesMinutes - ele.course_Minutes) / 60;
        });
        this.allCourses?.map((ele) => {
          ele.course_Minutes = ele.courseMinutes % 60;
          ele.course_hours = (ele.courseMinutes - ele.course_Minutes) / 60;
        });

        this.allData = this.allCourses;
        this.allDiplomas.forEach((diploma) => this.allData.push(diploma));
        this.allData = this.allData.sort(
          (a, b) => a.userCard_id - b.userCard_id
        );
        // console.log(this.allData);
      });
  }
}
