import { Component, OnInit } from "@angular/core";
import { Rate, Rates } from "app/shared/data/rate-list";
import { MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "app/shared/components/dialogs/delete-dialog/delete-dialog.component";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import {
  IAddEditCourseToCart,
  ICartCourse,
  ICartCourseList,
  RatetCourseRequest,
  RatetCourseResponse,
} from "app/student/models/shopping-cart";
import { ShoppingCartService } from "app/student/services/shopping-cart.service";
import { NgxSpinnerService } from "ngx-spinner";
import { UploadFileService } from "app/shared/services/General/upload-file.service";
import { AppAssets } from "app/shared/data/app-assets";
import { AppStrings } from "app/shared/data/app-strings";
import { CheckUserBuyCoursesDialogComponent } from "../check-user-buy-courses-dialog/check-user-buy-courses-dialog.component";
import { CorseCardCategoryM } from "app/home/models/home-course";

@Component({
  selector: "app-shopping-cart",
  templateUrl: "./shopping-cart.component.html",
  styleUrls: ["./shopping-cart.component.scss"],
})
export class ShoppingCart implements OnInit {
  courses: ICartCourse[] = [];
  TotalAmount: number = 0;
  rates: Rate[] = [];
  selectedCourses: number[] = [];

  constructor(
    public appAssets: AppAssets,
    public appStrings: AppStrings,
    public languageService: LanguageService,
    public fileService: UploadFileService,
    private shoppingCartService: ShoppingCartService,
    private toaster: MessagesService,
    private dialog: MatDialog,
    private spinner: NgxSpinnerService
  ) {
    this.rates = Rates;
  }

  ngOnInit(): void {
    this.getAllUserCartCourses();
  }

  onSelectCourse(checked: boolean, courseId: number) {
    let costIndex = this.courses.findIndex((e) => e.id === courseId);
    if (checked) {
      this.TotalAmount += this.courses[costIndex].cost;
      this.selectedCourses.push(courseId);
    } else {
      let i = this.selectedCourses.findIndex((e) => e === courseId);
      this.TotalAmount = this.TotalAmount - this.courses[costIndex].cost;
      this.selectedCourses.splice(i, 1);
    }
  }

  getAllUserCartCourses() {
    this.spinner.show();
    this.shoppingCartService
      .getAllUserCartCourses()
      .subscribe((res: ICartCourseList) => {
        if (res) {
          this.courses = res.userCardList;

          this.courses.map((ele) => {
            ele.rate = Number(ele.rate.toFixed(1));
          });
          this.courses.forEach((course) => {
            this.selectedCourses.push(course.id);
            this.TotalAmount += course.cost;
          });
          //this.TotalAmount = res.TotalAmount;
        }
        this.spinner.hide();
      });
  }

  onSubmit(id: number, subject_id: number) {
    this.spinner.show();

    let data = {} as IAddEditCourseToCart;
    data.id = id;
    data.subject_id = subject_id;

    this.shoppingCartService.addEditUserCartCourses(data).subscribe((res) => {
      if (res) {
        this.toaster.success("General.AddSuccessfully");
        this.spinner.hide();
      }
    });
  }

  openDeleteCourseDialog(course: ICartCourse): void {
    this.dialog
      .open(DeleteDialogComponent, {
        data: {
          msg: this.languageService.isArabic ? course.ar_name : course.en_name,
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.deleteCourseFromCart(course);
      });
  }

  deleteCourseFromCart(course: ICartCourse) {
    this.spinner.show();
    this.shoppingCartService
      .deleteCourseFromCart(course.userCard_id)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          let i = this.courses.indexOf(course);
          this.courses.splice(i, 1);
          this.toaster.success("General.DeleteSuccessfully");
          this.TotalAmount = this.TotalAmount - this.courses[i].cost;
        } else this.toaster.error(res?.returnString);
        this.spinner.hide();
      });
  }
  openChenKuserBuyCourseDialog(): void {
    this.dialog
      .open(CheckUserBuyCoursesDialogComponent, {
        data: {
          msg: "",
        },
      })
      .afterClosed()
      .subscribe((confirm) => {
        if (confirm) this.buyCourse();
      });
  }

  buyCourse() {
    let data: any;
    this.toaster.success("course.buySuccesed");

    this.shoppingCartService.buyCourse(data).subscribe((res) => {
      if (res) {
        this.toaster.success("course.buySuccesed");
      }
    });
  }

  getCategoresTooltip(categoryList: CorseCardCategoryM[]) {
    let Category: String[] = [];
    categoryList.forEach((ele, index) => {
      if (index != 0)
        Category.push(
          this.languageService.isArabic ? ele.ar_name : ele.en_name
        );
    });
    return Category.join("\n");
  }
}
